export default function NewsletterSubscription({ variant = 'full', searchParams = {} }) {
    const message = searchParams?.message ? decodeURIComponent(searchParams.message) : '';
    const type = searchParams?.type || '';

    // Compact version 
    if (variant === 'compact') {
        return (
            <div className="w-full max-w-md mx-auto">
                {message && (
                    <div className={`mb-3 p-3 rounded text-sm ${type === 'success'
                        ? 'bg-green-100 text-green-700 border border-green-200'
                        : 'bg-red-100 text-red-700 border border-red-200'
                        }`}>
                        {message}
                    </div>
                )}

                <form className="flex gap-0 newsletter-form">
                    <input
                        type="email"
                        name="email"
                        placeholder="Type your Email"
                        required
                        className="flex-1 px-4 py-2.5 border border-gray-300 rounded-l-md focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent text-gray-700 placeholder-gray-400 text-sm"
                    />
                    <button
                        type="submit"
                        className="bg-green-600 hover:bg-green-700 text-white font-medium px-6 py-2.5 rounded-r-md transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-green-500 text-sm whitespace-nowrap"
                    >
                        Subscribe
                    </button>
                </form>

                {/* Progressive Enhancement Script */}
                <script dangerouslySetInnerHTML={{
                    __html: `
                    (function() {
                        const form = document.querySelector('.newsletter-form');
                        if (!form) return;
                        
                        form.addEventListener('submit', async function(e) {
                            e.preventDefault();
                            
                            const email = form.email.value;
                            const button = form.querySelector('button');
                            const originalText = button.textContent;
                            
                            // Show loading state
                            button.textContent = 'Subscribing...';
                            button.disabled = true;
                            
                            try {
                                const response = await fetch('/api/newsletter', {
                                    method: 'POST',
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({ email }),
                                });
                                
                                const data = await response.json();
                                
                                // Remove existing message
                                const existingMessage = form.parentElement.querySelector('.message');
                                if (existingMessage) {
                                    existingMessage.remove();
                                }
                                
                                // Create new message
                                const messageDiv = document.createElement('div');
                                messageDiv.className = 'message mb-3 p-3 rounded text-sm ' + 
                                    (data.success ? 'bg-green-100 text-green-700 border border-green-200' : 'bg-red-100 text-red-700 border border-red-200');
                                messageDiv.textContent = data.message;
                                
                                form.parentElement.insertBefore(messageDiv, form);
                                
                                if (data.success) {
                                    form.reset();
                                }
                            } catch (error) {
                                console.error('Error:', error);
                                
                                // Remove existing message
                                const existingMessage = form.parentElement.querySelector('.message');
                                if (existingMessage) {
                                    existingMessage.remove();
                                }
                                
                                // Show error message
                                const messageDiv = document.createElement('div');
                                messageDiv.className = 'message mb-3 p-3 rounded text-sm bg-red-100 text-red-700 border border-red-200';
                                messageDiv.textContent = 'Something went wrong. Please try again.';
                                form.parentElement.insertBefore(messageDiv, form);
                            } finally {
                                button.textContent = originalText;
                                button.disabled = false;
                            }
                        });
                    })();
                    `
                }} />
            </div>
        );
    }

    // Full section version 
    return (
        <div className="w-full bg-gradient-to-r from-green-50 to-green-100 py-12 px-6">
            <div className="max-w-6xl mx-auto">
                <div className="bg-white rounded-lg shadow-sm p-8">
                    <h2 className="text-2xl font-bold text-gray-800 mb-3">
                        Subscribe to our Newsletter
                    </h2>
                    <p className="text-gray-600 mb-6">
                        Stay updated with the latest news and updates from the agricultural world.
                    </p>

                    {message && (
                        <div className={`mb-4 p-4 rounded-lg ${type === 'success'
                            ? 'bg-green-100 text-green-700 border border-green-200'
                            : 'bg-red-100 text-red-700 border border-red-200'
                            }`}>
                            {message}
                        </div>
                    )}

                    <form className="flex flex-col sm:flex-row gap-4 newsletter-form">
                        <input
                            type="email"
                            name="email"
                            placeholder="Your email address"
                            required
                            className="flex-1 px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent text-gray-700 placeholder-gray-400"
                        />
                        <button
                            type="submit"
                            className="bg-green-600 hover:bg-green-700 text-white font-semibold px-8 py-3 rounded-lg transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-offset-2 whitespace-nowrap"
                        >
                            Subscribe
                        </button>
                    </form>

                    {/* Progressive Enhancement Script */}
                    <script dangerouslySetInnerHTML={{
                        __html: `
                        (function() {
                            const form = document.querySelector('.newsletter-form');
                            if (!form) return;
                            
                            form.addEventListener('submit', async function(e) {
                                e.preventDefault();
                                
                                const email = form.email.value;
                                const button = form.querySelector('button');
                                const originalText = button.textContent;
                                
                                // Show loading state
                                button.textContent = 'Subscribing...';
                                button.disabled = true;
                                
                                try {
                                    const response = await fetch('/api/newsletter', {
                                        method: 'POST',
                                        headers: {
                                            'Content-Type': 'application/json',
                                        },
                                        body: JSON.stringify({ email }),
                                    });
                                    
                                    const data = await response.json();
                                    
                                    // Remove existing message
                                    const existingMessage = form.parentElement.querySelector('.message');
                                    if (existingMessage) {
                                        existingMessage.remove();
                                    }
                                    
                                    // Create new message
                                    const messageDiv = document.createElement('div');
                                    messageDiv.className = 'message mb-4 p-4 rounded-lg ' + 
                                        (data.success ? 'bg-green-100 text-green-700 border border-green-200' : 'bg-red-100 text-red-700 border border-red-200');
                                    messageDiv.textContent = data.message;
                                    
                                    form.parentElement.insertBefore(messageDiv, form);
                                    
                                    if (data.success) {
                                        form.reset();
                                    }
                                } catch (error) {
                                    console.error('Error:', error);
                                    
                                    // Remove existing message
                                    const existingMessage = form.parentElement.querySelector('.message');
                                    if (existingMessage) {
                                        existingMessage.remove();
                                    }
                                    
                                    // Show error message
                                    const messageDiv = document.createElement('div');
                                    messageDiv.className = 'message mb-4 p-4 rounded-lg bg-red-100 text-red-700 border border-red-200';
                                    messageDiv.textContent = 'Something went wrong. Please try again.';
                                    form.parentElement.insertBefore(messageDiv, form);
                                } finally {
                                    button.textContent = originalText;
                                    button.disabled = false;
                                }
                            });
                        })();
                        `
                    }} />
                </div>
            </div>
        </div>
    );
}
